import * as React from 'react';
import { omit } from 'lodash-es';
import {
  Plugin,
  RenderNodeProps,
  Block,
  BiPluginConfig,
} from '@ali/4ever-cangjie';
import MoTable from '../../mo/models';
import MoTableRow from '../../mo/models/tableRow';
import MoTableCell from '../../mo/models/tableCell';
import Table from './components/table';
import TableRow from '../components/tableRow';
import TableCell from '../components/tableCell';
import { TableWrapper } from '../components/styled';

export default function createRenderNode(
  config: BiPluginConfig,
): Plugin['renderNode'] {
  const tableConfig = config.table || {};
  // @ts-ignore
  return {
    [MoTable.TYPE]: (props: RenderNodeProps<MoTable>) => {
      const { node, visible } = props;

      if (!MoTable.isValidTable(node)) {
        return <></>;
      }
      return (
        <TableWrapper {...props.attributes} visible={visible}>
          <Table
            {...omit(props, 'attributes')}
            tableConfig={tableConfig}
          />
        </TableWrapper>
      );
    },
    [MoTableRow.TYPE]: (props) => {
      return <TableRow {...(props as RenderNodeProps<Block>)} />;
    },
    [MoTableCell.TYPE]: (props) => {
      return <TableCell {...(props as RenderNodeProps<MoTableCell>)} />;
    },
  };
}
